import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

declare interface RouteInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
}
export const ROUTES: RouteInfo[] = [
    { path: '/dashboard', title: 'Dashboard',  icon: 'ni-tv-2 text-primary', class: '' },
    { path: '/search', title: 'Search',  icon: 'ni-planet text-blue', class: '' },
    // { path: '/icons', title: 'Icons',  icon:'ni-planet text-blue', class: '' },
    // { path: '/maps', title: 'Maps',  icon:'ni-pin-3 text-orange', class: '' },
    // { path: '/user-profile', title: 'User profile',  icon:'ni-single-02 text-yellow', class: '' },
    // { path: '/tables', title: 'Tables',  icon:'ni-bullet-list-67 text-red', class: '' },
    // { path: '/login', title: 'Login',  icon:'ni-key-25 text-info', class: '' },
    // { path: '/register', title: 'Register',  icon:'ni-circle-08 text-pink', class: '' },
    { path: '/skills', title: 'Skills',  icon:'ni-align-left-2 text-blue', class: '' },
    { path: '/resources', title: 'Resources',  icon:'ni-circle-08 text-red', class: '' },
    { path: '/cohorts', title: 'Cohorts',  icon:'ni-bullet-list-67 text-blue', class: '' },
    { path: '/notification', title: 'Notification',  icon:'ni-bell-55 text-blue', class: '' },
    { path: '/invites', title: 'invites',  icon:'ni-notification-70 text-blue', class: '' },
];
export const resourceROUTES: RouteInfo[] = [
  { path: '/resource-dashboard/1', title: 'My profile',  icon:'ni-single-02 text-blue', class: '' },
];

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {

  public menuItems: any[];
  public isCollapsed = true;

  constructor(private router: Router) { 
  }

  isTrainerUrl: string = "/resource-dashboard/1";
  url = this.router.url;
  
  ngOnInit() {
    this.Sidemenu();
    // this.menuItems = resourceROUTES.filter(menuItem => menuItem);
    // this.menuItems = ROUTES.filter(menuItem => menuItem);
    this.router.events.subscribe((event) => {
        this.isCollapsed = true;
    });
  }
  
  // for changing sidemenus in sidebar
  Sidemenu(): void{
    if (this.isTrainerUrl == this.url) {
      this.menuItems = resourceROUTES.filter(menuItem => menuItem);
    } else {
      this.menuItems = ROUTES.filter(menuItem => menuItem);
    }
  }

}
